/* Main styles */

main {
  width: 100%;
  height: auto;
  padding: 30px 12.5%;
}

.main-container {
  width: 100%;
  height: auto;
  min-height: 500px;
  padding: 10px;
  border-radius: 5px;
  background: #434c5e;
}

._hidden {
  display: none;
}

.main-item {
  width: 100%;
  height: 100%;
  padding: 0 10px;
  font-family: "Roboto Mono";
  font-weight: 600;
}

/* TP-Styles */

.tp-title {
  font-size: 25px;
  color: #e5e9f0;
}

.tp-link {
  font-size: 16px;
  margin-right: 20px;
  transition: 0.2s linear;
  white-space: pre-line;
}

.tp-link:hover {
  text-shadow: 0 0 8px #e5e9f0;
}

.tp-link svg {
  margin-right: 5px;
}

.tp-image_block {
  margin: 30px 0 40px 0;
}

.tp-image {
  border-radius: 8px;
  width: 100%;
}

.tp-text {
  font-size: 18px;
  color: #d8dee9;
  white-space: pre-line;
}

.tp-sublinks_block {
  width: 100%;
  display: grid;
  grid-template: 1fr / repeat(3, 1fr);
  place-items: center;
  margin-top: 10px;
}

.tp-sublink {
  color: #d8dee9;
  transition: 0.2s linear;
  white-space: pre-line;
}

.tp-sublink:hover {
  text-shadow: 0 0 8px #d8dee9;
}

.tp-sublink::before {
  content: "#";
  display: inline-block;
  margin-right: 4px;
}

/* TP-Code styles */

.tp-code_block {
  width: 100%;
  height: auto;
  display: block;
}

.tp-code_container {
  width: 100%;
  height: auto;
}

.tp-code_box {
  padding: 26px 5px 5px 5px;
  background: #2e3440;
  border-radius: 8px;
  position: relative;
  border: 2px solid #4c566a;
  box-shadow: 0px 0px 8px 0 #4c566a;
  margin-bottom: 20px;
}

.tp-code_list {
  display: block;
  border-radius: 5px;
  width: 100%;
  background: #3b4252;
  border: 2px solid #3b4252;
  box-shadow: 0px 0px 8px 0 #3b4252;
  padding: 10px 10px 20px 10px;
  overflow-x: hidden;
}

.dots {
  position: absolute;
  left: 8px;
  top: 8px;
  display: grid;
  width: 52px;
  grid-template: 1fr / repeat(3, 1fr);
  grid-gap: 8px;
}

.dot-red,
.dot-yellow,
.dot-purple {
  display: block;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  cursor: pointer;
  transition: 0.2s linear;
}

.dot-red {
  background: #bf616a;
}

.dot-red:hover {
  box-shadow: 0 0 5px 0.5px #bf616a;
}

.dot-yellow {
  background: #ebcb8b;
}

.dot-yellow:hover {
  box-shadow: 0 0 5px 0.5px #ebcb8b;
}

.dot-purple {
  background: #b48ead;
}

.dot-purple:hover {
  box-shadow: 0 0 5px 0.5px #b48ead;
}

.tp-code {
  list-style: none;
  font-size: 16px;
  color: #88c0d0;
  font-weight: 600;
  line-height: 20px;
  width: 100%;
}

.tp-code_block {
  margin: 30px 0;
}

.result-title {
  font-size: 20px;
  margin-bottom: 10px;
  color: #8fbcbb;
  user-select: none;
}

.tab {
  display: block;
  margin-left: 20px;
  line-height: 10px;
}

.double-tab {
  display: block;
  margin-left: 40px;
  line-height: 10px;
}

.tp-code_list {
  border-radius: 5px 5px 0 0;
}

.result_list {
  border-radius: 0 0 5px 5px;
}

.result::before {
  content: ">  ";
  color: #ebcb8b;
  user-select: none;
}

/* Media questions */

@media (max-width: 940px) {
  .tp-title {
    font-size: 22px;
  }
  .tp-link {
    font-size: 13px;
  }
  .tp-sublink {
    font-size: 14px;
  }
  .tp-text {
    font-size: 15px;
  }
  .tp-code {
    font-size: 14px;
  }
  .result-title {
    font-size: 17px;
  }
}

@media (max-width: 820px) {
  .tp-code {
    font-size: 10px;
  }
  .result-title {
    font-size: 15px;
  }
}

@media (max-width: 740px) {
  .tp-text {
    font-size: 12px;
  }
}

@media (max-width: 590px) {
  .tp-code {
    font-size: 9px;
    line-height: 12px;
  }
  .tab,
  .double-tab,
  .triple-tab,
  .monster-tab {
    line-height: 3px;
  }
}

@media (max-width: 510px) {
  main {
    padding: 20px 0;
  }
  .main-container {
    border-radius: 0;
  }
}

@media (max-width: 430px) {
  .tp-text {
    font-size: 11px;
  }
}

/* The end of main styles */
