/* Compact menu styles */

menu {
  padding: 0;
  margin: 0 0 20px 0;
  font-family: "Roboto Mono", monospace;
  position: static;
  top: 0px;
}

.compact-menu {
  display: grid;
  width: 100%;
  height: 50px;
  background: #434c5e;
  border-radius: 5px;
  cursor: pointer;
}

.compact-menu_title:hover {
  background: #3b4252;
  color: #d8dee9;
  text-shadow: 0 0 5px #d8dee9;
}

.compact-menu_title {
  width: 100%;
  height: 100%;
  line-height: 50px;
  font-size: 25px;
  color: #e5e9f0;
  text-align: center;
  letter-spacing: 0.2em;
  transition: 0.2s linear;
}

.compact-menu_curtain {
  display: none; /* Or grid */
  position: absolute;
  background: rgba(0, 0, 0, 0.85);
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  align-content: center;
  cursor: auto;
  z-index: 100;
}

.curtain_active {
  display: grid;
}

.scroll-lock {
  overflow: hidden;
}

.compact-menu_list {
  list-style: none; /* Or grid */
  position: relative;
  width: 100%;
  height: auto;
  display: grid;
  grid-template: repeat(4, 1fr) / 1fr;
  padding-left: 0;
  place-content: center;
  grid-gap: 10px;
}

.compact-menu_item {
  height: 50px;
  width: 100%;
  height: 100%;
  display: grid;
  height: 60px;
  text-align: center;
  align-items: center;
}

.compact-menu_link {
  width: 100%;
  height: 100%;
  line-height: 60px;
  font-size: 25px;
  text-shadow: 0 0 2px #e5e9f0;
  transition: 0.2s linear;
}

.compact-menu_link::before {
  margin-right: 15px;
}

.compact-menu_link:hover {
  background: rgba(0, 0, 0, 0.9);
}

.compact-menu_cross {
  display: block;
  width: 30px;
  height: 30px;
  position: absolute;
  z-index: 10;
  right: 20px;
  top: 20px;
  cursor: pointer;
  transition: 0.3s linear;
}

.compact-menu_cross::before {
  content: "";
  display: block;
  width: 3px;
  height: 30px;
  border-radius: 5px;
  box-shadow: 0 0 5px 1px #e5e9f0;
  background: #e5e9f0;
  top: 0;
  left: 0;
  margin-left: 50%;
  transform: rotate(45deg);
  z-index: 10;
  display: block;
}

.compact-menu_cross::after {
  content: "";
  display: block;
  width: 3px;
  height: 30px;
  border-radius: 5px;
  box-shadow: 0 0 5px 1px #e5e9f0;
  background: #e5e9f0;
  margin: -100% 0 0 50%;
  transform: rotate(-45deg);
  z-index: 10;
  display: block;
}

/* Apply this */

@media (max-width: 510px) {
  .compact-menu {
    border-radius: 0;
  }
}

@media (max-width: 470px) {
  .compact-menu {
    height: 40px;
  }
  .compact-menu_title {
    line-height: 40px;
    font-size: 20px;
  }
}

/* The end of compact menu styles */
